import React from 'react';
import PropTypes from "prop-types";

const StarFullFilled = ({fill}) => (
	<svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill={fill}>
		<title>star</title>
		<path
			d="M10 1.3l2.388 6.722h6.412l-5.232 3.948 1.871 6.928-5.439-4.154-5.438 4.154 1.87-6.928-5.233-3.948h6.412l2.389-6.722z"/>
	</svg>
);

StarFullFilled.propTypes = {
	/** Fill color. */
	fill: PropTypes.string
};

export default StarFullFilled;


